#_pi_surveyWidgetContainer.mobile-enabled {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
}

#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget {
  width: 90%;
  position: relative;
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  border: 3px solid rgba(174, 174, 174, 0.6);
  border-radius: 5px;
}

#_pi_surveyWidgetContainer.mobile-enabled ._pi_closeButton {
  background-color: rgba(255, 255, 255, 0.1);
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
}

#_pi_surveyWidgetContainer.mobile-enabled ._pi_widgetContentContainer {
  margin-top: 10px;
}

div._pi_free_text_question_field_container {
  margin: 0px 18px 10px 18px;
}

input._pi_free_text_question_field,
textarea._pi_free_text_question_field {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  width: 100%;
  padding: 0px 2px;
  border: none;
  border-bottom: 1px solid #57636e;
  background-color: transparent;
  outline: none;
  box-shadow: none;
  font-size: 15px;
  color: #ccd1dd;
}

textarea._pi_free_text_question_field {
  border: 1px solid #57636e;
}

input._pi_free_text_question_field:hover,
textarea._pi_free_text_question_field:hover {
  border-bottom: 1px solid #8fa9b6;
}

textarea._pi_free_text_question_field:hover {
  border: 1px solid #8fa9b6;
}

div._pi_free_text_question_submit_button_container {
  margin: 10px 0px 0px 0px;
}

input._pi_free_text_question_submit_button {
  background-color: black;
  font-size: 15px;
  padding: 5px 20px;
  border: none;
  color: #eaf4fb;
  cursor: pointer;
}

input._pi_free_text_question_submit_button:hover {
  background-color: #101010;
}

input._pi_free_text_question_submit_button:disabled {
  opacity: 0.3;
}

input._pi_free_text_question_submit_button:hover:disabled {
  background-color: black;
  opacity: 0.3;
}

._pi_free_text_question_characters_count {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  text-align: right;
  font-size: 12px;
  color: #7a8992;
}

._pi_free_text_question_characters_count.danger,
._pi_free_text_question_characters_count.error {
  color: #927a85;
}

._pi-control {
  display: block;
  position: relative;
  margin: 0px 0px 0px 25px;
  padding: 5px 0px 5px 0px;
}

._pi-control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

._pi-control-indicator {
  position: absolute;
  top: 9px;
  left: -25px;
  height: 15px;
  width: 15px;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

._pi-control-indicator:after {
  content: "";
  position: absolute;
  display: none;
}

._pi-control input:checked~._pi-control-indicator:after {
  display: block;
}

._pi-control-checkbox ._pi-control-indicator:after {
  left: 4px;
  width: 5px;
  height: 11px;
  border: solid rgba(255, 255, 255, 0.5);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

div._pi_free_text_question_field_container {
  clear: both;
  margin: 0px 18px 10px 18px;
}

ul._pi_answers_container li label input._pi_checkbox {
  margin-right: 10px;
  cursor: pointer;
}

ul._pi_answers_container li label {
  color: white;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  width: 100%;
  width: calc(100% - 35px);
  float: left;
  cursor: pointer;
}

div._pi_multiple_choices_question_submit_button_container {
  margin: 10px 0px 0px 0px;
  clear: both;
}

input._pi_multiple_choices_question_submit_button {
  background-color: black;
  font-size: 15px;
  padding: 5px 20px;
  border: none;
  color: #eaf4fb;
  cursor: pointer;
}

input._pi_multiple_choices_question_submit_button:hover {
  background-color: #101010;
}

input._pi_multiple_choices_question_submit_button:disabled {
  opacity: 0.3;
}

input._pi_multiple_choices_question_submit_button:hover:disabled {
  background-color: black;
  opacity: 0.3;
}

._pi_multiple_choices_count {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-size: 12px;
  color: #927a85;
  height: 17px;
}

._pi_closeButton {
  padding: 0;
}

#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget {
  top: 0px;
  left: 0px;
  width: 100% !important;
  height: 100% !important;
  position: fixed;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-direction: column !important;
  flex-direction: column !important;
  -ms-flex-wrap: nowrap !important;
  flex-wrap: nowrap !important;
  -ms-flex-pack: justify !important;
  justify-content: space-between !important;
  -ms-flex-align: center !important;
  align-items: center !important;
  -ms-flex-line-pack: center !important;
  align-content: center !important;
  border: none;
  margin: 0;
}